import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			orderNo: '',
			mallDomain: '',
			payInfo: {},
			initData: {}
		}
	},
	methods: {
		onBridgeReady(){　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　//使用微信浏览器内置的对象调起微信支付插件，并传入统一接口返回的参数
			WeixinJSBridge.invoke(
			  'getBrandWCPayRequest', {
				"appId":this.payInfo.appId,                 //公众号名称，由商户传入
				"timeStamp":this.payInfo.timeStamp,         //时间戳，自1970年以来的秒数
				"nonceStr":this.payInfo.nonceStr,           //随机字符串
				"package":this.payInfo.package,            //支付验证pay_id
				"signType":this.payInfo.signType,                           //微信签名方式
				"paySign":this.payInfo.paySign                 //微信签名
			  },
			  function(res){
				alert(res)
				if(res.err_msg == "get_brand_wcpay_request:ok" ){
				  // 使用以上方式判断前端返回,微信团队郑重提示：
				  //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
				  console.log('res.err_msg == "get_brand_wcpay_request:ok"')
				}
			  }
			)
		},
		Gopay(){　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　　//点击付款按钮开始支付
			console.log("开始支付")
			if (typeof WeixinJSBridge == "undefined"){
			  if( document.addEventListener ){
				document.addEventListener('WeixinJSBridgeReady', this.onBridgeReady(), false);
			  }else if (document.attachEvent){
				document.attachEvent('WeixinJSBridgeReady', this.onBridgeReady());
				document.attachEvent('onWeixinJSBridgeReady', this.onBridgeReady());
			  }
			}else{
			  this.onBridgeReady();
			}
		},
		handleSubmit(){
			// let data = JSON.parse(uni.getStorageSync('PAYMENT_ORDER_INFO'));
			let data = {
				// ...this.baseInfo,
				accountId: this.initData.accountId,
				addressId: this.initData.addressId,
				balance: this.initData.balance,
				isBalance: this.initData.isBalance,
				credits: 0,
				expressFee: this.initData.expressFee,
				goodsList: this.initData.goodsList,
				payPrice: this.initData.payPrice,
				remark: this.initData.remark,
				totalPrice: this.initData.totalPrice,
				payChannels: '6',
				callBackNo: this.orderNo,
				mallDomain: this.mallDomain,
				mallId:2
			};
			this.$http.post('/mall/app/order/submit', data, {
				header: {
					Authorization: 'Bearer eyJhbGciOiJSUzI1NiIsInR5cCI6IkpXVCJ9..OON7DvWhohAPYJodJsbEs7xKl8O3WvqHts_-FM4aRv9ET1kPOJfmqNRtTLCGbit5gkdDpYjER0J4l6EWhrqyWBIlAaH7_SFCspiebeW8cS9VoK3LctL_2rZaQvZRUNM6rXGTMLi-LfGmrTIlN9uzcHEIavxoC305a5tFgViMEsXB3YficlujGBVk7Cwmo6N7jLZPUdTXV1N_O1I-Six93JAkDkUj1KAyt8l6UxUhDCVwCOQbtCWHeFvbOh0H5jAr7dG0yJZLtc7hOw8b-WEkBa1OhtuVd925_-sR-nM_utkjExJixR8OmKmezgm_7E17gH7F7sqHvdQTP1FeV8tWGQ'
				}
			})
			.then( res => {
				console.log('WxWebPay  接口调用成功')
				if(res.code == 0){
					this.payInfo.appId = res.result.payResponse.wxPayResponse.appId;
					this.payInfo.timeStamp = res.result.payResponse.wxPayResponse.timeStamp;
					this.payInfo.nonceStr = res.result.payResponse.wxPayResponse.nonceStr;
					this.payInfo.package = res.result.payResponse.wxPayResponse.packageStr;
					this.payInfo.signType = res.result.payResponse.wxPayResponse.signType;
					this.payInfo.paySign = res.result.payResponse.wxPayResponse.paySign;
					this.onBridgeReady();
				}
			})
			.catch( err => {
				console.log(err);
			})
		},
		getInitData(){
			this.orderNo=uni.getStorageSync('ORDER_NO');
			// this.initData=JSON.parse(uni.getStorageSync('PAYMENT_ORDER_INFO'));
			this.initData=JSON.parse(uni.getStorageSync('paymentOrder'));
			this.mallDomain=uni.getStorageSync('mallDomain');
		}
	},
	computed: {
		...mapState('common', ['baseInfo', 'userInfo']),
		
	},
	onLoad(options){
		uni.setStorageSync('ORDER_NO', options.orderNo);
		uni.setStorageSync('mallDomain', options.mallDomain);
		uni.setStorageSync('paymentOrder', options.paymentrder);
		this.getInitData();
	},
}
